* {
  box-sizing: border-box;
}
html {
  scroll-behavior: smooth;
}
body {
  margin: 0;
  padding: 0;
}
a {
  color: inherit;
}
/*рамки кнопок после нажатия черные это видимо состояние focus */
.header-menu-fon {
  background-color: #1B2D37;
}
.header-menu-fon h1,
.header-menu-fon p,
.header-menu-fon li {
  color: #FFF;
}
.menu__button {
  background-color: rgba(255, 255, 255, 0);
  color: #FFF;
  border: 1px solid #FFF;
}
.menu__button:hover {
  color: #00B2FF;
  border: 1px solid #00B2FF;
}
.header__button {
  border: 1px solid #00B2FF;
  background-color: #00B2FF;
  color: #FFF;
}
.header__button:hover {
  background-color: #FFF;
  color: #00B2FF;
}
.header__item a > p {
  color: rgba(255, 255, 255, 0.5);
}
.advantages-about-company-fon {
  background-color: #FFF;
}
.advantages__item {
  background-color: #FFF;
  border: 1px solid #EBEBEB;
  border-left: 2px solid #00B2FF;
}
.advantages__item h4 {
  color: #00B2FF;
}
.advantages__item p {
  color: #666666;
}
.advantages__item:hover {
  background-color: #00B2FF;
}
.advantages__item:hover svg path {
  fill: #FFF;
}
.advantages__item:hover h4 {
  color: #FFF;
}
.advantages__item:hover p {
  color: #FFF;
}
.about-company_text h3,
.about-company_text p {
  color: #666666;
}
.about-company__button {
  border: 1px solid #00B2FF;
  background-color: #FFF;
  color: #00B2FF;
}
.about-company__button:hover {
  background-color: #00B2FF;
  color: #FFF;
}
.about-company__button:hover {
  border: 1px solid #FFF;
}
.services {
  background-color: #1B2D37;
}
.services h2,
.services p {
  color: #FFF;
}
.services__production,
.services__montage,
.services__complect {
  background-blend-mode: overlay;
  background-color: rgba(27, 45, 55, 0.7);
}
.services__button {
  background-color: #00B2FF;
  color: #FFF;
  border: 1px solid #00B2FF;
}
.consult {
  background-color: #00B2FF;
  color: #FFF;
}
.consult input,
.consult textarea {
  background-color: rgba(255, 255, 255, 0.1);
  color: #FFF;
  border: 1px solid #00B2FF;
}
.consult input:focus,
.consult textarea:focus {
  border: 1px solid #FFF;
}
.consult__button {
  background-color: #00B2FF;
  color: #FFF;
  border: 1px solid #FFF;
}
.consult__button:hover {
  background-color: #FFF;
  color: #00B2FF;
}
.consult__button:hover {
  border: 1px solid #00B2FF;
}
.footer {
  background-color: #FFF;
}
.footer h4 {
  color: #1B2D37;
}
.footer p {
  color: rgba(27, 45, 55, 0.5);
}
.footer__bottom-container {
  border-top: 1px solid rgba(98, 105, 98, 0.2);
}
@font-face {
  font-family: 'PT Mono';
  src: url('../fonts/PTMono-Regular.woff2') format('woff2'), url('../fonts/PTMono-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Roboto Condensed';
  src: url('../fonts/RobotoCondensed-Regular.woff2') format('woff2'), url('../fonts/RobotoCondensed-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Roboto Condensed';
  src: url('../fonts/RobotoCondensed-Bold.woff2') format('woff2'), url('../fonts/RobotoCondensed-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}
p,
li,
span {
  font-family: 'PT Mono', monospace;
}
h1,
h2,
h3,
h4,
h5 {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: normal;
}
button {
  font-family: 'Roboto Condensed', sans-serif;
  font-weight: bold;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
}
a {
  text-decoration: none;
}
.menu ul li span {
  font-size: 16px;
  line-height: 18px;
}
