/*позиционирование треугольников*/
.header {
  background: url("../img/header-fon.svg") right 60px top no-repeat;
  padding: 10% 2% 5% 3%;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 1023px) {
  .header {
    background-position: right -100px top -100px;
    background-size: 502px 502px;
    padding: 10% 0 7% 3%;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .header {
    background-position: right -250px top -100px;
    background-size: 480px 480px;
    padding: 10% 2% 13% 2%;
    flex-direction: column-reverse;
    align-items: center;
  }
}
.header__button {
  width: 407px;
  height: 55px;
  padding: 17px 34px;
  border-radius: 5px;
  margin-top: 30px;
  margin-left: 90px;
}
@media (min-width: 768px) and (max-width: 1023px) {
  .header__button {
    width: 80%;
    margin-top: 60px;
    margin-left: 50px;
  }
}
@media (min-width: 320px) and (max-width: 767px) {
  .header__button {
    width: 90%;
    margin-top: 45px;
    margin-left: 0;
  }
}
.header__button:hover {
  cursor: pointer;
}
@media (min-width: 320px) and (max-width: 767px) {
  .header__button_desctop {
    display: none;
  }
}
.header__button_mobile {
  display: none;
}
@media (min-width: 320px) and (max-width: 767px) {
  .header__button_mobile {
    display: inline;
  }
}
.header__item h1 ~ p {
  padding-left: 90px;
  background: url("../img/header_rect.svg") no-repeat;
  background-position: left 3px top 21px;
}
@media (max-width: 1023px) {
  .header__item h1 ~ p {
    background-size: 33px;
    padding-left: 50px;
    background-position: left 3px top 17px;
  }
}
@media (max-width: 767px) {
  .header__item h1 ~ p {
    background-position: left 3px top 16px;
  }
}
.header__item a > p {
  transform: rotate(-90deg);
  margin-top: 100px;
  margin-left: -40px;
  width: 93px;
  height: 16px;
}
@media (max-width: 1023px) {
  .header__item a > p {
    display: none;
  }
}
.header__item:last-child {
  background: url("../img/panel.png") no-repeat;
  width: 100%;
  max-width: 705px;
  height: 515px;
}
@media (max-width: 1023px) {
  .header__item:last-child {
    width: 50%;
    background-size: 465px 340px;
  }
}
@media (max-width: 767px) {
  .header__item:last-child {
    background-size: 301px 220px;
    background-position: right;
    width: 100%;
    height: 220px;
    margin-bottom: -40px;
  }
}
.advantages-about-company-fon {
