
.advantages-about-company-fon {
	background-image: url("../img/advantages-fon.svg");
	background-repeat: no-repeat;
	background-position: left -184px top 134px;
	padding: 9% 11% 7%;

	@media (min-width: 768px) and (max-width: 1023px) {
		padding: 7% 3% 6%;
	}

	@media (min-width: 320px) and (max-width: 767px) {
		padding: 11% 5% 6%;
	}
}

.advantages {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;

	@media (min-width: 320px) and (max-width: 767px) {
	flex-direction: column;
	align-items: center;
	}
}

.advantages img {
	max-width: 36px;

	@media (max-width: 1023px) {
	max-width: 32px;
	}
}


.advantages__item {
	width: 47%;
	padding: 40px 43px 49px 45px;
	margin: 10px;

	@media (min-width: 768px) and (max-width: 1023px) {
	width: 46%;
	padding: 25px 26px 11px 26px;
	}

	@media (min-width: 320px) and (max-width: 767px) {
	width: 100%;
	padding: 25px 12px 16px 27px;
	}

	&:hover {
	cursor: pointer;
	}
}	


.about-company {
	margin-top: 5%;
	display: flex;
	
	img {
		
	@media (min-width: 320px) and (max-width: 767px) {
	display: none;
	}
	}
}

.about-company_text {
	margin-top: -25px;

/*кнопки вынести в variables*/
	.about-company__button {
	border-radius: 5px;
	margin-top: 20px;
	padding: 15px 50px;
	
	&:hover {
	cursor: pointer;
	}
	}	
}
